
import { IonContent, IonHeader, IonPage, IonToolbar, IonSegment, IonSegmentButton, IonButtons, IonButton, IonIcon } from '@ionic/vue';
import { defineComponent } from 'vue';
import { 
  documentsOutline,
  trashOutline
} from 'ionicons/icons';
import { Clipboard } from '@ionic-native/clipboard';
import { Keyboard } from '@ionic-native/keyboard';

export default defineComponent({
  name: 'Home',
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonToolbar,
    IonSegment,
    IonSegmentButton,
    IonButtons,
    IonButton,
    IonIcon,
  },
  data: function() {
    return {
      selectedSegment: 'english',
      engText: '',
      encodeText: '',
    }
  },
  mounted(){
    Keyboard.show();
  },
  methods: {
    segmentChanged(ev: CustomEvent) {
      this.selectedSegment =  ev.detail.value;
    },
    engTextChange(ev: CustomEvent) {
      this.engText = ev.detail.target.value != null ? ev.detail.target.value : '';
      //encode the code
      const encoded: any = {
        a: '⅃', b: 'ⵡ', c: 'L',
        d: 'コ', e: 'ロ', f: 'ⵎ',
        g: 'ᒣ', h: '⨅', i: 'ᒥ',
        j: 'ᒲ', k: '⨃', l: 'ᒷ',
        m: 'ᑔ', n: '⚀', o: 'ᑢ',
        p: 'ךּ', q: '⩀', r: '⟔',
        s: 'ⴸ', t: 'く', u: 'ᨈ',
        v: '＞', w: 'ᨉ', x: 'ᑅ',
        y: 'ᨊ', z: 'ᐷ'
      };

      const value: string = this.engText.toLowerCase();
      let cipher = '';
      for(let i = 0 ; i < value.length; i++){
        if (value[i] in encoded) {
          cipher += encoded[value[i]];
        } else {
          cipher += value[i];
        }
      }
      this.encodeText = cipher;
      // console.log(encoded[value]);
    },
    encodeTextChange(ev: CustomEvent) {
      this.encodeText = ev.detail.target.value != null ? ev.detail.target.value : '';
      //decode the code
      const decoded: any = {
        '⅃': 'a',  'ⵡ': 'b',  'L': 'c',
        'コ': 'd',  'ロ': 'e',  'ⵎ': 'f',
        'ᒣ': 'g',  '⨅': 'h',  'ᒥ': 'i',
        'ᒲ': 'j',  '⨃': 'k',  'ᒷ': 'l',
        'ᑔ': 'm',  '⚀': 'n',  'ᑢ': 'o',
        'ךּ': 'p',  '⩀': 'q',  '⟔': 'r',
        'ⴸ': 's',  'く': 't',  'ᨈ': 'u',
        '＞': 'v',  'ᨉ': 'w',  'ᑅ': 'x',
        'ᨊ': 'y',  'ᐷ': 'z'
      }

      const value: string = this.encodeText.toLowerCase();
      let decipher = '';
      for(let i = 0 ; i < value.length; i++){
        if (value[i] in decoded) {
          decipher += decoded[value[i]];
        } else {
          decipher += value[i];
        }
      }
      this.engText = decipher;
    },
    copyText(segment: any) {
      console.log(segment);
      if (segment === 'english') {
        Clipboard.copy(this.engText);
      }
    },
    clearInputs() {
      this.engText = '';
      this.encodeText = '';
    }
  },
  setup() {
    return {
      documentsOutline,
      trashOutline
    }
  }
});
